import React from "react";
import Card from "react-bootstrap/Card";

function AboutCard() {
	return (
		<Card className="quote-card-view">
			<Card.Body>
				<blockquote className="blockquote mb-0">
					<p style={{ textAlign: "justify" }}>
						<span className="purple">Dveloppeur Web professionnel </span>
						et diplm, je possde galement un diplme d'ingnieur des Arts et
						Mtiers ainsi qu'un master en Administration des Entreprises (IAE).
						<br />
						<br />
						Grce  cette formation  360 et  mes 10 ans d'exprience
						professionnelle dans l'ingnierie, j'allie{" "}
						<span className="purple">matrise technique</span> et parfaite
						comprhension de la{" "}
						<span className="purple">gestion d'un projet</span>
						.
						<br />
						<br />
						J'interviens actuellement comme freelance sur tous vos projets Front
						et/ou Back.
					</p>
				</blockquote>
			</Card.Body>
		</Card>
	);
}

export default AboutCard;
